body {
  color: #333;
  background-color: #f4f4f4;
  margin: 20px;
  font-family: Arial, sans-serif;
}

select.breed-select {
  margin-bottom: 10px;
  padding: 8px;
  font-size: 16px;
  display: none;
}

.loader {
  height: 12px;
  width: 80%;
  border: 1px solid #fff;
  border-radius: 10px;
  display: block;
  position: relative;
  overflow: hidden;
}

.loader:after {
  content: "";
  width: 40%;
  height: 100%;
  box-sizing: border-box;
  background: #969496;
  animation: animloader 2s linear infinite;
  position: absolute;
  top: 0;
  left: 0;
}

@keyframes animloader {
  0% {
    left: 0;
    transform: translateX(-100%);
  }

  100% {
    left: 100%;
    transform: translateX(0%);
  }
}

.error {
  color: red;
  font-size: 16px;
  font-weight: bold;
  display: none;
}

.cat-info {
  background-color: #fff;
  border-radius: 8px;
  margin-top: 20px;
  padding: 20px;
  display: none;
  overflow: hidden;
  box-shadow: 0 0 10px rgba(0, 0, 0, .1);
}

.cat-info img {
  max-width: 720px;
  float: left;
  border-radius: 8px;
  margin-right: 20px;
  box-shadow: 0 0 5px rgba(0, 0, 0, .1);
}

.cat-info p {
  margin-bottom: 10px;
  font-size: 16px;
  line-height: 1.6;
}

.cat-info span {
  color: #333;
  font-weight: bold;
}

